<template>
    <div class="tickets">
        <loading-overlay v-if="dataLoading" />

        <div
            v-if="!tickets.total && !dataLoading"
            class="no-results"
        >
            <img
                src="@/assets/img/svg/Questions.svg"
                alt="Questions"
            >
            <h1 class="no-results__title no-results__title_question">
                No issues
            </h1>
            <p class="no-results__description">
                Create a new support ticket, if you need help.
            </p>
        </div>

        <div
            v-for="(ticket, index) in tickets.data"
            :key="index"
            class="tickets_card"
        >
            <div class="tickets_card__info">
                <router-link
                    v-if="ticket.orderid"
                    :to="{
                        name: 'details',
                        params: {
                            id: ticket.orderid
                        },
                        query: {
                            tab: 'tickets',
                            referenceid: ticket.ticketid
                        }
                    }"
                    class="tickets_card__title"
                >
                    <h2>
                        <span class="tickets_card__title-index">
                            {{ `${offset + index + 1}. ` }}
                        </span>
                        «<span class="tickets-title-text">{{ ticket.subject || 'Untitled' }}</span>»
                        <span class="break-xs">
                            <span
                                class="tickets_card__title--status"
                                :class="{
                                    'tickets_card__title--status__success': ticket.status_support === 'OPEN',
                                    'tickets_card__title--status__error': ticket.status_support !== 'OPEN'
                                }"
                            >
                                {{ ticket.status_support }}
                            </span>
                        </span>
                    </h2>
                </router-link>
                <h2
                    v-else
                    class="tickets_card__title"
                >
                    <span class="tickets_card__title-index">
                        {{ offset + index + 1 }}.
                    </span>

                    «{{ ticket.subject || 'Untitled' }}»

                    <span
                        class="tickets_card__title--status"
                        :class="{
                            'tickets_card__title--status__success': ticket.status_support === 'OPEN',
                            'tickets_card__title--status__error': ticket.status_support !== 'OPEN'
                        }"
                    >
                        {{ ticket.status_support }}
                    </span>
                </h2>

                <div
                    class="tickets_card__metainfo"
                    :class="{ 'show-info': ticket.showMessages }"
                >
                    <span class="tickets_card__metainfo-data">
                        <span class="tickets_card__metainfo-data-title">
                            {{ ticket.created_at.date | moment_bidding_text }}
                        </span>
                        <span class="tickets_card__metainfo-data-counter">
                            <img
                                src="@/assets/img/svg/clock.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="mdi"
                            >
                            {{ ticket.created_at.date | moment_from }} ago
                        </span>
                    </span>

                    <span
                        v-if="ticket.ticketid"
                        class="tickets_card__metainfo-data"
                    >
                        <span class="tickets_card__metainfo-data-title">
                            Ticket ID
                        </span>
                        <span class="tickets_card__metainfo-data-counter">
                            <img
                                src="@/assets/img/svg/clipboard.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="mdi"
                            >
                            {{ ticket.ticketid }}
                        </span>
                    </span>

                    <span
                        v-if="ticket.orderid"
                        class="tickets_card__metainfo-data"
                    >
                        <span class="tickets_card__metainfo-data-title">
                            Order ID
                        </span>
                        <span class="tickets_card__metainfo-data-counter">
                            <img
                                src="@/assets/img/svg/clipboard.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="mdi"
                            >
                            {{ ticket.orderid }}
                        </span>
                    </span>

                    <span
                        v-if="ticket.messages.length > 0"
                        class="tickets_card__metainfo-data tickets_card__metainfo-data--md0"
                    >

                        <span class="tickets_card__metainfo-data-title">
                            Comments
                        </span>
                        <span class="tickets_card__metainfo-data-counter">
                            <img
                                src="@/assets/img/svg/message-square.svg"
                                alt="Deadline"
                                width="20px"
                                height="20px"
                                class="mdi"
                            >
                            {{ ticket.messages.length }}
                            <span
                                v-if="ticket.unread_messages.length > 0"
                                class="orange-text"
                            >
                                +{{ ticket.unread_messages.length }} <sup>new</sup>
                            </span>
                        </span>

                        <span class="show-info__detail" />
                    </span>

                    <a
                        href="#"
                        class="tickets_card__toggle-btn"
                        @click.prevent="toggle(index, ticket.ticketid)"
                    >
                        <chevron-up v-if="ticket.showMessages" />
                        <chevron-down v-else />
                    </a>
                </div>

                <transition name="slide-y-transition">
                    <div
                        v-if="ticket.showMessages"
                        class="tickets_card__messages"
                    >
                        <div
                            v-for="(message, message_index) in ticket.messages"
                            :key="message_index"
                            class="tickets_card__messages-item"
                            :class="{ 'tickets_card__messages-item__support': message.owner !== 'client' }"
                        >
                            <div class="tickets_card__messages-item__inside">
                                <div class="tickets_card__messages-item__content">
                                    <div class="tickets_card__messages-item__content-header">
                                        <span
                                            v-if="message.created_at_local"
                                            class="tickets_card__messages-item__content-header__time"
                                        >
                                            {{ message.created_at_local.date | moment_from }} ago <br>
                                            <small>
                                                {{ message.created_at_local.date | moment_short_text }}
                                            </small>
                                        </span>
                                        <span
                                            v-if="message.owner === `client`"
                                            class="tickets_card__messages-item__content-header__name"
                                        >
                                            {{ `${firstname}` }}
                                        </span>
                                        <span
                                            v-else
                                            class="tickets_card__messages-item__content-header__name"
                                        >
                                            Support
                                        </span>
                                    </div>
                                    <div
                                        class="tickets_card__messages-item__content-text"
                                        v-html="message.details"
                                    />
                                </div>

                                <div class="tickets_card__messages-item__avatar">
                                    <div
                                        v-if="message.owner === 'client'"
                                        class="tickets_card__messages-item__avatar-user"
                                    >
                                        <span>{{ `${firstname.substr(0, 1) + lastname.substr(0, 1)}` }}</span>
                                    </div>
                                    <div
                                        v-else
                                        class="tickets_card__messages-item__avatar-service"
                                    />
                                </div>
                            </div>
                        </div>

                        <div class="tickets_card__messages-actions">
                            <form-input
                                v-model="message"
                                legend="Message"
                                type="text"
                                name="message"
                                :field.sync="message"
                                :errors="validationMessage($v.message)"
                                @input.native="$v.message.$touch()"
                                @blur.native="$v.message.$touch()"
                                @keyup.native="keyListner($event)"
                            />
                            <button
                                class="btn-base btn-base_normal btn-base_colored tickets_card__messages-actions-btn"
                                @click.prevent="sendMessage(ticket.ticketid)"
                            >
                                <button-loader v-if="loadingBtn" />
                                Reply
                            </button>
                        </div>
                    </div>
                </transition>

                <a
                    href="#"
                    class="tickets_card__toggle-btn_mobile"
                    @click.prevent="toggle(index, ticket.ticketid)"
                >
                    <span
                        v-if="ticket.showMessages"
                        class="btn-base btn-base_normal btn-base_border btn-base_border-ticket"
                    >hide</span>

                    <span
                        v-else
                        class="btn-base btn-base_normal btn-base_border btn-base_border-ticket"
                    >Show more</span>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
// Helpers
import Api from '@/helpers/api/index.js';
import { forEach, findIndex } from 'lodash'
import { eventBus } from '@/helpers/event-bus/'

// Mixins
import filtersMixin from '@/mixins/filtersMixin.js'

// Validation
import { validationMixin } from 'vuelidate'
import { validationMessage } from 'vuelidate-messages'
import { required } from 'vuelidate/lib/validators'
import { formMessages } from '@/validation/support/Messages'

// Icons
import ChevronUp from 'mdi-vue/ChevronUp';
import ChevronDown from 'mdi-vue/ChevronDown';

// Components
import LoadingOverlay from '@/components/LoadingOverlay';
import ButtonLoader from '@/components/common/ButtonLoader';

// Vuex types
import { ACTION_TICKET_ASK_REPLY, ACTION_TICKET_SUBMIT_REPLY } from '@/store/modules/support/action-types';
import { CLEAR_MESSAGE } from '@/store/modules/support/mutation-types';

// vuex bindings
import { createNamespacedHelpers } from 'vuex'
import { mapSupportFields } from '@/store/modules/support';
import { mapClientFields } from '@/store/modules/client'

const {
    mapMutations: mapSupportMutations,
    mapActions: mapSupportActions
} = createNamespacedHelpers('support');

export default {
    components: {
        ChevronUp,
        ChevronDown,
        LoadingOverlay,
        ButtonLoader
    },
    mixins: [
        filtersMixin,
        validationMixin
    ],
    props: {
        orderid: {
            type: String
        }
    },
    validations: {
        message: { required }
    },
    data() {
        return {
            page: this.$router.currentRoute.params.page || 1,
            dataLoading: false,
            tickets: [],
            loadingBtn: false
        }
    },
    computed: {
        ...mapClientFields([
            'client_data.id'
        ]),
        ...mapSupportFields([
            'form_data.message'
        ]),
        offset() {
            return this.tickets.per_page * (this.page - 1)
        },
        firstname() {
            return this.$store.state.client.client_data.firstname
        },
        lastname() {
            return this.$store.state.client.client_data.lastname
        }
    },
    async mounted() {
        await this.getData()
        if (this.$route.query.referenceid) {
            const { referenceid } = this.$route.query
            const index = findIndex(this.tickets.data, (o) => o.ticketid === referenceid);
            this.toggle(index, referenceid)
        }
        eventBus.$on('showSnackBar', (message) => {
            if (message === 'A support ticket has been created. You can view all tickets in your account (Support Tickets).') {
                this.getData()
            }
        })
    },
    methods: {
        ...mapSupportActions({
            actionTicketAskReply: ACTION_TICKET_ASK_REPLY,
            actionTicketSubmitReply: ACTION_TICKET_SUBMIT_REPLY
        }),
        ...mapSupportMutations({
            clearMessage: CLEAR_MESSAGE
        }),
        toggle(val, ticketid) {
            this.$v.$reset()
            const vm = this
            const state = this.tickets.data[val].showMessages
            this.clearMessage()
            forEach(vm.tickets.data, (ticket) => {
                // eslint-disable-next-line no-param-reassign
                ticket.showMessages = false
            })
            this.tickets.data[val].showMessages = !state
            vm.$forceUpdate()

            setTimeout(() => {
                vm.markAsRead(val, ticketid)
            }, 1000)
        },
        markAsRead(val, ticketid) {
            return new Promise((resolve, reject) => {
                const vm = this
                Api.post('/api/cp/ticket-support/mark-ticket-messages-as-read', {
                    ticketid
                })
                    .then(() => {
                        // vm.decrementTicket(vm.tickets.data[val].unread_messages.length)
                        vm.tickets.data[val].unread_messages = []
                        vm.$root.$emit('updateCounter');
                        resolve(true)
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
        },
        keyListner(e) {
            if (e.which === 13 && this.message.length) {
                this.sendMessage()
            }
        },
        async sendMessage(value) {
            this.$v.$touch()
            if (this.message.length === 0) { return }
            const vm = this;
            vm.actionTicketAskReply({
                id: value
            });

            vm.loadingBtn = true;
            const response = await vm.actionTicketSubmitReply({
                id: value
            });
            if (response) {
                vm.updateMessage(value)
                    .then(() => {
                        eventBus.$emit('showSnackBar', 'Your message has been received', 'success');
                        vm.message = '';
                        // vm.cpTicketReplyOff()
                    })
                    .catch((error) => {
                        eventBus.$emit('showSnackBar', error.message, 'error');
                    })
                    .then(() => {
                        vm.$v.$reset();
                        vm.loadingBtn = false;
                    })
            }
        },
        validationMessage: validationMessage(formMessages),
        async getData() {
            console.log(this.orderid)
            this.dataLoading = true;
            const vm = this
            await Api.post('/api/cp/ticket-support/list-tickets', {
                orderid: vm.orderid,
                per_page: 10,
                clientid: vm.id
            })
                .then((response) => {
                    vm.tickets = response.data
                })
                .then(() => {
                    vm.dataLoading = false;
                })
        },
        updateMessage(id) {
            return new Promise((resolve, reject) => {
                const vm = this
                Api.post('/api/cp/ticket-support/fetch', {
                    ticketid: id
                })
                    .then((response) => {
                        const index = findIndex(vm.tickets.data, (o) => o.ticketid === id);
                        vm.tickets.data[index].messages = response.data[0].messages
                        resolve(true)
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
        }
    }
}
</script>
